                        '重庆邮电大学', '重庆交通大学', '重庆医科大学', '重庆师范大学', '重庆文理学院', '重庆三峡学院',
                        '长江师范学院', '四川外国语大学', '西南政法大学', '四川美术学院', '重庆科技学院', '重庆理工大学',
                        '重庆工商大学'
                    ],
                    '天津': [
                        '天津科技大学', '天津工业大学', '天津理工大学', '天津农学院', '天津医科大学', '天津中医药大学',
                        '天津师范大学', '天津职业技术师范大学', '天津外国语大学', '天津商业大学', '天津财经大学', '天津体育学院',
                        '天津音乐学院', '天津美术学院', '天津城建大学', '天津大学', '南开大学', '河北工业大学'
                    ],
                    '西藏自治区': [
                        '西藏大学'
                    ]
                },
                second_active: '北京大学',
                photo: '',
                photo_code: '',
                message: '发送验证码', // 短信验证码按钮文字
                timer: null, // 定时器清除
                button_disabled: false, // 按钮禁用
            }
        },
        async mounted() {
            this.$utils.setDocumentTitle('创新创业大讲堂');
            // 获取 openid
            if (this.getOpenid_info === '') {
                await this.$store.dispatch('_getOpenid', {
                    im: this.$config.PROJECT_INTERFACE.getopenid,
                    url: this.$config.REQUEST_URL
                })
            }

            // 设置 PV
            this.$store.dispatch('_setPv', {
                im: this.$config.PROJECT_INTERFACE.set_pv_statistics,
                fps: {
                    info_key: '组织组册',
                },
                url: this.$config.REQUEST_URL
            });

        },
        methods: {
            /**
             * 获取验证码
             * */
            getCode() {

                if (this.photo === '') {
                    this.$Toast('电话不能为空');
                    return false;
                }

                this.$Toast.loading({
                    message: '发送中...',
                    forbidClick: true,
                    duration: 0,
                    overlay: true
                });

                this.$store.dispatch('_getPhotoCode', {
                    im: this.$config.PROJECT_INTERFACE.send_phone_identifying_code,
                    fps: {
                        open_id: this.getOpenid_info.back_value.open_id,
                        phonenum: this.photo
                    },
                    url: this.$config.REQUEST_URL
                }).then(res => {
                    if (res.back_value) {
                        this.$Toast.clear();
                        const TIME_COUNT = 60;
                        if (!this.timer) {
                            let code = TIME_COUNT;
                            this.button_disabled = true;
                            this.message = `${TIME_COUNT}s后获取`;
                            this.timer = setInterval(() => {
                                if (code > 0 && code <= TIME_COUNT) {
                                    code--;
                                    this.message = `${code}s后获取`;
                                } else {
                                    clearInterval(this.timer);
                                    this.timer = null;
                                    this.message = '发送验证码';
                                    this.button_disabled = false;
                                }
                            }, 1000);
                        }
                    } else {
                        this.$Toast.clear();
                        if (res.error_code === 6180516006) {
                            this.$Toast(res.error_info);
                        }
                    }
                })


            },
            /**
             * 选择组织
             * */
            changFirst(event) {
                let optionVal = event.target.value;

                if (optionVal === '其他') {
                    this.seconde_tip = '请输入大学';
                    this.second_active = ''
                } else {
                    this.seconde_tip = '请选择大学';
                    this.first_active = optionVal;
                    this.second_active = this.second_list[optionVal][0];
                }
            },
            /**
             * 提交选择的组织
             * */
            submit() {

                if (this.photo_code === '') {
                    this.$Toast('验证码不能为空');
                    return false;
                }


                this.show = true;
                this.$store.dispatch('_setOrganization', {
                    im: this.$config.PROJECT_INTERFACE.set_user_organization,
                    fps: {
                        open_id: this.getOpenid_info.back_value.open_id,
                        level_1_organization: this.first_active,
                        level_2_organization: this.second_active,
                        phonenum: this.photo,
                        phonecode: this.photo_code
                    },
                    url: this.$config.REQUEST_URL
                }).then(res => {
                    if (res.back_value) {
                        this.show = false;
                        this.$router.push('/')
                    } else {
                        this.show = false;
                    }
                });
            }
        },
        computed: {
            ...mapGetters([
                'getOpenid_info'
            ]),
            second() {
                return this.second_list[this.first_active]
            }
        }
    }
</script>

